    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }






    onSubmit() {
        let input: any;
        this.formSumitAttempt = true;
        // if (this.form.valid) {
        input = { form_data: this.form.value };
        input.form_data.user_id = this.currentUser.success.id;
        input.form_data.product_img = this.productImgUrl;


        this._productService.updateProduct(input).subscribe(
            data => {
                this.product_id = data.success;
                this.toastr.success('Product Update Successfully', 'Success Message');
                this.reset();
                this.router.navigate(['product']);
            },
        );
        //  }
    }



